import React from 'react';

import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

// import components
import HomePage from './pages/Home';
import PortfolioPage from './pages/Portfolio';
import ContactPage from './pages/Contact';
import Header from './components/Header';
import Footer from './components/Footer';
import BackTopBtn from './components/BackTopBtn';

const App = () => {
  return (
    <div className='bg-white relative'>
      <Router>
      <Header />
      <Routes>
        <Route path={`${process.env.PUBLIC_URL}`} exact element={<HomePage />}/>
        <Route path={`${process.env.PUBLIC_URL}/home`} exact element={<HomePage />}/>
        <Route path={`${process.env.PUBLIC_URL}/portfolio`} exact element={<PortfolioPage />}/>
        <Route path={`${process.env.PUBLIC_URL}/contact`} exact element={<ContactPage />}/>
      </Routes>
      <Footer />
      <BackTopBtn />
      </Router>
    </div>
  );
};

export default App;
